import requests from './httpService';

const NotificationServices = {
  addNotification(body) {
    return requests.post('/notifications/add', body);
  },

  getAllNotifications() {
    return requests.get('/notifications?admin=1');
  },
  getNotificationById(id) {
    return requests.get(`/notifications/${id}?admin=1`);
  },
  updateStatus(id, body) {
    return requests.put(`/notifications-status/${id}?admin=1`, body);
  },
  updateNotification(id, body) {
    return requests.put(`/notifications/${id}?admin=1`, body);
  },
  deleteNotification(id) {
    return requests.delete(`/notifications/${id}`);
  },
};

export default NotificationServices;
